<template>
<div class="form-wrapper">
    <form v-if="product" class="form-add-edit">
      <h2>
          
      </h2> 
      <span class="required">-  </span>

      <div class="row">
        <label class="short-label">:</label>
        <input id="product-name" v-model="product.name" class="form__input" type="text" placeholder=""/>
      </div>

      <div class="row">
        <label class="short-label">:</label>
          <select id="product-category"  class="form__input" v-model="product.category">
            <option v-for="type in productTypes" :key="type">{{ type }}</option>
          </select>
      </div>

      <div class="row">
        <label class="short-label" >:</label>
          <textarea id="product-description" class="form__input" v-model="product.description" placeholder=" "/>
      </div>

      <div class="row">
        <label class="short-label"> :</label>
          <input id="product-image" class="form__input" type="file" ref="file"/>
      </div>

      <div class="row">
        <label class="short-label"> : </label>
          <input type="number" id="product-price" class="form__input" v-model="product.price" placeholder=" "/>
      </div>

      <div class="row">
        <label class="short-label">:</label>
          <select id="product-manufacturer" class="form__input" v-model="product.manufacturer" placeholder=" " >
            <option v-for="manufacturer in manufacturerList" :key="manufacturer">{{ manufacturer }}</option>
          </select>
      </div>

      <div class=form-add-edit__controls>
        <button id="save" class="form__btn" type="submit" @click.prevent="validateForm(true)"> </button>
        <button id="save-new" class="form__btn" @click.prevent="validateForm(false)">     </button>
        <button id="cancel" class="form__btn" @click.prevent="cancel()"></button>
      </div>
    </form>
  </div>
</template>

<script>
import { showNoty,  PRODUCT_CATEGORIES} from "../utility";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  name: "ProductAddEdit",

  data() {
    return {
      product: {
        name: "",
        category: " ",
        description: "",
        srcImage:"",
        price:"",
        manufacturer:"",
      },
      isLoading:false,
      productTypes: PRODUCT_CATEGORIES,
      manufacturerList:["Bosch", "Samsung"],
    };
  },

  validations: {
    product: {
      name: { required },
      description: { required, max: maxLength(2048) }
    }
  },

  computed: {
    isEdit() {
      return this.$route.params && this.$route.params.id;
    }
  },

  async mounted() {
    if (this.isEdit) {
      await this.getTask();
    }
  },

  methods: {
    /**
     *     
     *
     */
    async getTask() {
      try {
        const response = await this.$http.get("products/" + this.$route.params.id);

        if (response.data === null) {
          this.$router.push({ name: "product-list" });
          showNoty("  ");
          return;
        }

        this.product = response.data;
      } catch (error) {
        showNoty(error);
      }
    },

    /**
     *  
     *
     *@param {string} response -  
     *
     */
    async saveTask(isComplete) {
      try {
        const response = this.isEdit
          ? await this.$http.put("products", this.product)
          : await this.$http.post("products", this.product);

        if (this.checkErrors(response) && isComplete) {
          this.$router.push({
            path: this.isEdit ? "/product/" + this.product.id : "/"
          });
          showNoty(
            `Product ${response.data.name} ` +
              (this.isEdit ? "" : ""),
            "success"
          );
          return;
        }
      } catch (e) {
        showNoty("   .   !");
        return;
      }

      this.resetForm();
    },

    /**
     *  
     *
     */
    resetForm() {
      this.product = {
        name: "",
        category: " ",
        description: "",
        tag: "",
        dateOfTask: ""
      };
    },

    /**
     *  
     *
     */
    cancel() {
      // 
      this.$router.go(-1);
    },

    /**
     *     
     * @param {object} response -  
     *
     */
    checkErrors(response) {
      if (response.data.message === "success") {
        return true;
      }

      response.data.errors.forEach(error =>
        showNoty(this.transformErrorMessage(error.message))
      );

      return false;
    },

    /**
     *   
     * @param {string} response -  
     *
     */
    transformErrorMessage(message) {
      message = message.replace(".", " ");
      message = message.replace(
        /([A-Z])/,
        (match, p1) => " " + p1.toLowerCase()
      );
      message = message.charAt(0).toUpperCase() + message.slice(1) + ".";

      return message;
    },

    /**
     *    
     * @param {boolean} isComplete -        .
     *
     */
    validateForm(isComplete) {
      this.$v.product.$touch();
      if (this.$v.product.$error) {
        if (this.$v.product.name.$error) {
          showNoty("   ");
        }

        if (this.$v.product.description.$error) {
          showNoty(
            "         2048 "
          );
        }
        showNoty("     -   ");
      } else {
        this.saveTask(isComplete);
      }
    }
  }
};
</script>

<style lang="scss" scoped>

@import "../scss/components/ProductAddEdit.scss";


</style>
